       "synop":"2014년 4월 16일 중국 도심에 떠있는 유령선\r\n'세월호' 진실을 감추기 위한 천 개의 거짓말\r\n\r\n2014년 4월 16일 아침, 세월호가 침몰했고 304명이 희생됐다. 검찰은 ‘선원의 운전 미숙으로 발생한 단순 사고’라고 발표했다. 2018년 4월, 세월호의 침몰 원인을 다룬 다큐멘터리 <그날, 바다>는 선박이 자동으로 송신하는 항해 데이터인 세월호 AIS 데이터가 조작됐다는 증거들을 제시했고 전문가들도 반박하지 못했다. 그러나 메이저 언론들은 믿을 수 없다는 듯이 침묵으로 일관했다. 2018년 12월, 다큐팀이 4년 전 국회에 제출된 데이터에서 정부 서버에는 존재할 수 없는 스웨덴 선박 데이터들을 발견한다. 다큐팀은 스웨덴 선박을 추적했고 데이터에 담긴 선박의 위치 정보는 중국 선전시 한복판으로 나왔다. 스웨덴 선박은 역시 유령선이었다. 그리고 참사 당일 사고해역을 운항한 선박 1천 척의 데이터가 조작됐다. 누가 세월호 AIS 데이터 조작을 지시했는가? 기획자는 당시 한국 정부와 관련돼 있는가? 그리고 그들은 데이터 조작을 통해 무엇을 감추려고 했는가?\r\n\r\n",
